body {
  overflow: auto;
  overflow-y: scroll;
}
body.menu_open {
  overflow: hidden;
  padding-right: 0.5rem;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background-color: var(--secondary);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--black);
  border-radius: 1rem;
}

blockquote, ol, p, ul {
  font-size: 1rem;
  line-height: 1.75;
  margin: 0 0 1rem;
}

a {
  text-decoration: underline;
  transition: color 150ms ease-in-out;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.3;
  margin: 3rem 0 1.38rem;
  font-weight: 700;
}

img {
  height: auto;
  max-height: 95vh;
  width: 100%;
}

figure {
  margin: 0;
}

input {
  padding: 0.75rem 1rem;
}

button {
  background-color: var(--primary);
  border: none;
  border-radius: 2px;
  color: var(--white);
  margin: 0.5rem 0;
  padding: 0.75rem 1rem;
}

.button_link {
  border-radius: 2px;
  box-shadow: 2px 2px 10px 0 rgba(70, 94, 112, 0.5);
  display: inline-block;
  margin: 25rem auto;
  padding: 12.5rem 25rem;
  text-transform: uppercase;
  transition: all 150ms ease-in-out;
}
.button_link:hover, .button_link:focus {
  box-shadow: 1px 1px 15px 0px rgba(70, 94, 112, 0.7);
}
.button_link:active {
  box-shadow: 2px 2px 10px 0 rgba(70, 94, 112, 0.5);
}

::-moz-selection {
  background: var(--secondary);
  color: #fff;
}

::selection {
  background: var(--secondary);
  color: #fff;
}

.visually_hidden, .screen-reader-text, .skip_to_content {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.visually_hidden.focusable:active, .focusable.skip_to_content:active, .focusable.screen-reader-text:active,
.visually_hidden.focusable:focus,
.focusable.skip_to_content:focus,
.focusable.screen-reader-text:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.content__narrow_width {
  width: 60vw;
  width: clamp(16rem, 100% - 2rem, 60rem);
  margin-left: auto;
  margin-right: auto;
  padding: 0;
}

.content__site_width, #masthead + .woofoos_breadcrumb, .bottom, .top {
  width: 90vw;
  width: clamp(16rem, 100% - 2rem, 75rem);
  margin-left: auto;
  margin-right: auto;
  padding: 0;
}

.content__full_width {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

.top {
  position: relative;
  display: grid;
  grid-template-columns: 15% 25% 60%;
  margin-top: 1rem;
}

button.top_menu__toggle {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0 0.5rem;
  position: relative;
  transition: all 150ms linear 300ms;
  z-index: 100;
}

.top_logo {
  display: inline-block;
  align-self: center;
  transition: all 150ms linear;
  z-index: 100;
}
.top_logo svg {
  fill: var(--black);
  height: 2rem;
  transition: all 150ms linear;
  width: 100%;
}
.top_logo a {
  display: inline-block;
}
.top_logo a:hover svg, .top_logo a:focus svg {
  fill: var(--primary);
}

.search_form {
  color: var(--black);
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column: 3/span 2;
  grid-row: 1;
  height: 3rem;
  margin: 0;
  padding: 0 0.5rem;
  position: relative;
  z-index: 100;
}
.search_form label {
  color: var(--black);
  opacity: 1;
  position: absolute;
  top: calc(50% - 0.5rem);
  left: 0.5rem;
  transition: 100ms linear;
}
.search_form input[type=text] {
  background: none;
  border: 0 none;
  height: 100%;
  outline: none;
  padding: 1rem 0 0 0;
  vertical-align: middle;
  width: 100%;
}
.search_form input[type=text]:focus ~ label, .search_form input[type=text]:valid ~ label {
  top: 10px;
  font-size: 0.75rem;
}
.search_form input[type=submit] {
  background: none;
  border: none;
  color: var(--black);
  font-weight: 700;
  padding: 0;
}

.top_cart {
  z-index: 100;
}
.top_cart ul, .top_cart p {
  margin-bottom: 0;
}

.woocommerce-mini-cart {
  list-style: none;
  padding: 0;
  text-align: left;
}

.top_features {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column: 3/span 2;
  grid-row: 2;
  z-index: 100;
}

.top_menu {
  background: var(--white);
  height: 100vh;
  overflow: scroll;
  padding: 7rem 0 1rem;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate3d(-100vw, 0, 0);
  transition: transform 0.3s cubic-bezier(0, 0.52, 0, 1);
  width: 100vw;
  z-index: 99;
}
.top_menu > ul {
  background-color: var(--white);
  list-style: none;
  padding-left: 1rem;
  position: relative;
}
.top_menu.show {
  transform: translate3d(0vw, 0, 0);
}

.top_menu::-webkit-scrollbar {
  width: 0.5rem;
}

.top_menu::-webkit-scrollbar-thumb {
  background-color: var(--black);
  border-radius: 1rem;
}

@media only screen and (min-width: 900px) {
  body::-webkit-scrollbar {
    width: 1rem;
  }

  .top {
    align-items: start;
    grid-template-columns: 50% repeat(2, 25%);
    position: relative;
  }

  .top_logo {
    align-self: end;
    grid-column: 1/span 1;
    grid-row: 1/span 2;
  }
  .top_logo svg {
    height: 4rem;
    width: auto;
  }

  .search_form {
    grid-column: 2/span 2;
    grid-row: 2;
    padding: 0 1rem;
  }
  .search_form label {
    left: 1rem;
  }

  .top_cart {
    display: inline-block;
    padding: 0.5rem 0;
  }

  .top_features {
    grid-column: 2/span 2;
    grid-row: 1;
  }

  .top_menu__toggle {
    display: none;
  }

  .top_menu {
    grid-column: 1/span 3;
    grid-row: 3/span 1;
    height: auto;
    margin: 2rem 0 0;
    overflow: visible;
    padding: 0;
    position: initial;
    top: initial;
    left: initial;
    transform: translate3d(0vw, 0, 0);
    width: 100%;
  }
  .top_menu a {
    background-color: transparent;
    font-size: 21px;
    text-decoration: none;
  }
  .top_menu > ul {
    display: flex;
    margin: 0;
    padding: 0;
    position: relative;
    left: 0;
    width: 100%;
  }
  .top_menu > ul li {
    font-weight: 700;
    height: 100%;
    line-height: 1;
    padding: 0 0.5rem;
    text-align: center;
  }
  .top_menu > ul li:first-child {
    padding-left: 0;
  }
}
.bottom {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.woofoos_breadcrumb {
  display: inline-block;
  margin: 1rem 0;
}
.woofoos_breadcrumb span {
  display: inline-block;
  padding-right: 0.25rem;
}
.woofoos_breadcrumb span:after {
  content: " | ";
}
.woofoos_breadcrumb span:last-of-type:after {
  content: "";
  padding-right: 0;
}
.woofoos_breadcrumb a {
  display: inline-block;
  line-height: 1;
}

#masthead + .woofoos_breadcrumb {
  display: block;
}

@media only screen and (min-width: 900px) {
  #masthead + .woofoos_breadcrumb {
    border-top: none;
  }
}
.content__with_sidebar {
  display: flex;
}

.with_sidebar__main {
  flex: 0 1 75%;
}

#sidebar {
  flex: 0 1 25%;
}

.product_list__container,
.search_list__container {
  display: grid;
  grid-column-gap: 25rem;
  grid-row-gap: 50rem;
  grid-template-columns: repeat(auto-fit, minmax(285px, 1fr));
}
.product_list__container a,
.search_list__container a {
  color: inherit;
  text-decoration: none;
}
.product_list__container a:hover .post__thumb, .product_list__container a:focus .post__thumb,
.search_list__container a:hover .post__thumb,
.search_list__container a:focus .post__thumb {
  box-shadow: 0px 0px 50px -45px rgba(0, 0, 0, 0.7);
}
article.post {
  margin: 0;
}
article.post div.post__thumb {
  padding-bottom: 56.25%;
  transition: box-shadow 300ms ease-in-out;
}
article.post h2 {
  font-size: 21px;
  line-height: 1;
  margin: 0.5em 0;
  transition: color 300ms ease-in-out;
}
article.post ul {
  border-left: 3px solid var(--grey-light);
  list-style: none;
  margin: 0;
  padding: 0 0 0 0.5rem;
}

.single-product div.product {
  margin: 3rem 0;
}
.single-product div.product h1 {
  line-height: 1;
  margin: 1rem 0;
}
.single-product div.product .price {
  margin: 0;
}

.product__gallery {
  background-color: var(--grey-light);
  padding-bottom: 56.25%;
}

.product_meta {
  margin: 50rem 0;
}
.product_meta > span {
  display: block;
  line-height: 1.5;
  text-align: left;
  text-transform: lowercase;
}

.related.products {
  margin: 0 auto;
  max-width: 900px;
  width: 100%;
}
.related.products ul {
  display: grid;
  grid-column-gap: 1rem;
  grid-row-gap: 2rem;
  grid-template-columns: repeat(3, 33.3334%);
  list-style: none;
  margin: 0;
  padding: 0;
}

@media only screen and (min-width: 900px) {
  .single-product div.product {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50%, 1fr));
  }
  .single-product div.product .product__gallery {
    grid-column: 1/span 1;
    grid-row: 1/span 2;
  }
  .single-product div.product .product_meta {
    margin: 0;
  }
  .single-product div.product .product__details {
    grid-column: 2/span 1;
    grid-row: 1;
    padding-left: 1rem;
    text-align: right;
  }
  .single-product div.product .product__details ul {
    border-left: none;
  }
  .single-product div.product .woocommerce-tabs {
    grid-column: 2/span 1;
    grid-row: 2;
    text-align: right;
  }
  .single-product div.product section.related {
    grid-column: 1/span 2;
    grid-row: 3;
  }
}
.pagination {
  margin: 50rem auto;
  max-width: 900px;
  width: 100%;
}
.pagination ul {
  display: flex;
  justify-content: center;
  list-style: none;
}
.pagination li {
  margin: 0 600rem;
}
.pagination li.active a {
  background-color: var(--grey-dark);
}
.pagination a {
  background-color: var(--primary);
  display: inline-block;
  font-weight: 700;
  padding: 12.5rem 25rem;
  text-decoration: none;
  transition: background-color 150ms ease-in-out;
}
.pagination a:hover, .pagination a:focus, .pagination a:active {
  background-color: var(--grey-dark);
}